.icon-facebook { width: 0.53em; height: 1em; fill: currentColor; }

body { font-family: 'Roboto', sans-serif; font-size: 16px; font-weight: 400; line-height: 2.0625; color: #627791; background-color: #F2F8FF; }

.title-lg { color: #000; font-size: 84px; font-weight: 700; line-height: 1.07; }

.title-md { color: #000; font-size: 50px; font-weight: 700; line-height: 1.2; }

.title { color: #000; font-size: 36px; font-weight: 700; line-height: 1.8; }

.description { font-size: 18px; font-weight: 500; line-height: 1.778; }

.color-blue { color: #3852CE; }

.text-center { text-align: center; }

.section { padding: 178px 0; }

.section_about { padding-bottom: 0; }

.section__wrap { max-width: 488px; }

.section__row { margin: 0 -10px; display: flex; }

.section__row:not(:first-child) { margin-top: 103px; }

.section__img { padding: 0 10px; width: 50%; }

.section__img img, .section__img svg { display: block; max-width: 100%; }

.section__img_shad img { border-radius: 5px; box-shadow: 0 20px 40px 0 rgba(107, 154, 212, 0.1); }

.section__img_fixw img { max-width: 960px; }

.section__img_center { justify-content: center; display: flex; }

.section__content { padding: 38px 10px 0; width: 50%; }

.section__content_pt0 { padding-top: 0; }

.section__content p:not(:first-child) { margin-top: 23px; }

.section__content p:not(:last-child) { margin-bottom: 29px; }

.section__content .description + .btn { margin-top: 29px; }

.section .title { margin-bottom: 11px; }

.section .description { margin-bottom: 15px; }

.out { position: relative; overflow-x: hidden; }

.inner { margin: 0 auto; padding: 0 20px; max-width: 1180px; }

.block { display: flex; }

.header { position: absolute; top: 0; left: 0; width: 100%; z-index: 10; padding: 33px 0 27px; color: #627791; }

.header__inner { margin: 0 auto; padding: 0 20px; max-width: 1600px; width: 100%; flex-wrap: wrap; align-items: center; display: flex; }

.header__logo { margin-right: 40px; flex-shrink: 0; }

.header__wrap { flex-grow: 1; align-items: center; justify-content: space-between; display: flex; }

.header__nav { padding-left: 40px; position: relative; }

.header__nav:before { position: absolute; content: ''; top: 0; left: 0; bottom: 0; width: 1px; background-color: rgba(101, 113, 128, 0.15); }

.header__right { align-items: center; flex-wrap: wrap; display: flex; }

.header__right .btn:not(:last-child) { margin-right: 20px; }

.header__right .lang:not(:first-child) { margin-left: 30px; }

.logo { width: 148px; }

.logo img { display: block; max-width: 100%; }

.nav { text-transform: uppercase; }

.nav__list { flex-wrap: wrap; display: flex; }

.nav li:not(:last-child) { margin-right: 40px; }

.nav a { color: currentColor; opacity: .7; transition: opacity .3s, color .3s; }

.no-touch .nav a:hover { opacity: 1; }

.nav a.is-active { opacity: 1; color: #000; }

.lang { position: relative; color: #627791; font-size: 14px; font-weight: 500; }

.no-touch .lang:hover .lang__list { opacity: 1; visibility: visible; }

.no-touch .lang:hover .lang__current:before { transform: rotate(180deg); }

.lang img { margin: 5px 10px 0 0; max-width: 18px; max-height: 18px; display: block; }

.lang__current { position: relative; display: flex; width: 100%; cursor: pointer; padding-right: 13px; }

.lang__current:before { position: absolute; content: ''; top: 50%; right: 0; margin-top: -2px; transition: transform .3s; width: 0px; height: 0px; border-style: solid; border-width: 4px 4px 0 4px; border-color: #627791 transparent transparent transparent; }

.lang__list { position: absolute; right: 0; top: 100%; padding-top: 5px; min-width: 90px; max-width: 120px; opacity: 0; visibility: hidden; transition: opacity 0.3s, visibility 0.3s; word-break: break-all; word-break: break-word; hyphens: auto; }

.lang__list li { display: block; }

.lang__list li:not(:first-child) { margin-top: 5px; }

.lang__list a { display: flex; color: currentColor; transition: opacity .3s; }

.no-touch .lang__list a:hover { opacity: .7; }

.lang__list a img { margin-top: 5px; }

.btn { padding: 12px 40px; font-family: 'Roboto', sans-serif; font-size: 16px; font-weight: 700; line-height: 2.5; color: #fff; border-radius: 5px; text-transform: uppercase; display: inline-block; background-color: #4C68EF; transition: background-color .3s, box-shadow .3s, border-color .3s, color .3s; }

.no-touch .btn:hover { background-color: #1436db; }

.btn_sm { padding: 11px 34px; line-height: 1.72; font-size: 14px; }

.btn_sm.btn_bord { padding-top: 9px; padding-bottom: 9px; }

.btn_bord { color: #4C68EF; background-color: transparent; border: 2px solid #4C68EF; }

.no-touch .btn_bord:hover { color: #fff; border-color: #1436db; background-color: #1436db; }

.btn_shad { box-shadow: 0 20px 40px 0 rgba(76, 104, 239, 0.35); }

.no-touch .btn_shad:hover { box-shadow: 0 10px 20px 0 rgba(76, 104, 239, 0.35); }

.hero { position: relative; padding: 103px 0; min-height: 913px; overflow: hidden; background-image: linear-gradient(224deg, #EDF6FF 0%, #E2EFFF 100%); }

.hero:before { position: absolute; content: ''; z-index: 2; bottom: 0; left: 50%; transform-origin: right bottom; transform: translateX(-50%) skewY(-3deg); height: 122px; width: 100vw; background-color: #F2F8FF; }

.hero .title-lg { margin-bottom: 13px; }

.hero__wrap { position: relative; width: 100%; display: flex; }

.hero__content { padding-top: 110px; max-width: 503px; }

.hero__content p:not(:first-child) { margin-top: 25px; }

.hero__content p:not(:first-child) { margin-bottom: 28px; }

.hero__img { position: absolute; top: 0; right: 0; margin-right: -175px; }

.hero__img svg { margin-top: 115px; max-width: 776px; width: auto; height: auto; display: block; }

.hero [data-anim="from-bottom"] { position: relative; }

[data-anim] { position: relative; }

[data-anim-text-parent] { overflow: hidden; display: block; }

[data-anim="text-from-bottom"] { display: block; transform: translateY(102%); }

[data-anim="from-bottom"] { transform: translateY(70px); opacity: 0; }

[data-anim="from-top"] { transform: translateY(-70px); opacity: 0; }

[data-anim="fade"] { opacity: 0; }

.together { position: relative; padding: 125px 0; }

.together:before { position: absolute; content: ''; top: 0; left: 0; width: 100%; height: 100%; transform-origin: center; transform: skewY(-3deg); background-image: linear-gradient(224deg, #EDF6FF 0%, #E2EFFF 100%); }

.together__inner { position: relative; z-index: 2; }

.together__title { margin-bottom: 45px; }

.together__row { margin: 0 -20px; flex-wrap: wrap; display: flex; }

.together__col { position: relative; padding: 0 20px; flex: 0 0 33.3%; min-width: 33.3%; }

.together__col:not(:last-child):before { position: absolute; content: ''; top: 0; right: 0; height: 100%; width: 1px; background-color: rgba(205, 221, 253, 0.6); }

.together__icon { margin-bottom: 13px; height: 128px; width: 128px; border-radius: 50%; box-shadow: 0 20px 40px 0 rgba(107, 154, 212, 0.1); background-color: #fff; align-items: center; justify-content: center; display: inline-flex; }

.together__icon img { max-width: 90%; }

.together__numb { font-size: 30px; font-weight: 700; line-height: 1.84; color: #000; text-transform: uppercase; }
